import React from 'react';
import './HomePage.css';
import sockImage from './../../images/WhiteSockMedium.png';

import HomePageBanner from './home_page_banner/HomePageBanner'

const HomePage = () => {
    return (
        <main className="home-page-container">
            <HomePageBanner />
            <div className="home-page-products-container">
                <div className="home-page-product home-page-sock">
                    <img alt='sock' src={sockImage}></img>
                </div>
                <div className="home-page-product"></div>
            </div>
        </main>
    )
}

export default HomePage;
