import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { loginUser } from '../../actions/actions';
import localforage from 'localforage';

import './LoginPage.css'

class LoginPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
            loginFailedAlert:false
        }
    }

    render() {
        return (
            <div className="login-page-container">
                <div className="login-form-container">
                    <div className="login-form">
                        <p>Login</p>
                        <input value={this.state.username} onChange={input => this.updateUsername(input)} placeholder="Username" />
                        <br />
                        <input value={this.state.password} onChange={input => this.updatePassword(input)} type="password" placeholder="Password" />
                        <br />
                        <button onClick={() => { this.login() }}>Login</button>
                        {!window.navigator.onLine ? <p className="login-failed-alert">Internet is required to login</p> : null}
                        {this.state.loginFailedAlert ? <p className="login-failed-alert">Login failed</p> : null}
                    </div>
                    <Link to='/new'>
                        <div className="create-account">
                            New? Create account
                        </div>
                    </Link>
                </div>
            </div>
        );
    }

    updateUsername(input) {
        this.setState({
            username: input.target.value
        });
    }

    updatePassword(input) {
        this.setState({
            password: input.target.value
        });
    }

    login() {
        fetch('https://webshop-api.herokuapp.com/user', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({username:this.state.username, password: this.state.password})
            })
            .then(res => {
                if (res.ok) {
                    return res.json()
                }
                throw new Error("Inloggen is mislukt")
            })
            .then(data => {
                this.props.loginUser(data)
                localforage.setItem('user', data)
                this.props.history.push("/all")
            })
            .catch(err => {
                console.log(err)
                this.setState({loginFailedAlert: true})
            });
    }
}

function matchDispatchToProps(dispatch) {
    return bindActionCreators({ loginUser: loginUser }, dispatch)
}

export default connect(null, matchDispatchToProps)(LoginPage);