import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { toggleSideNav } from '../../actions/actions';
import './SideNav.css';

class SideNav extends Component {
    render() {
        return (
            <nav id="navbar" className={this.props.sideNavOpen ? 'slide-in' : 'slide-out'}>
                <ul>
                    <li onClick={() => this.props.toggleSideNav()}><Link to='/all'>All socks</Link></li>
                    <li onClick={() => this.props.toggleSideNav()}><Link to='/women'>Women</Link></li>
                    <li onClick={() => this.props.toggleSideNav()}><Link to='/men'>Men</Link></li>
                    <li onClick={() => this.props.toggleSideNav()}><Link to='/kids'>Kids</Link></li>
                    <li onClick={() => this.props.toggleSideNav()}><Link to='/contact'>Contact</Link></li>
                </ul>
            </nav>
        )
    }
}

function mapStateToProps(state) {
    return {
        sideNavOpen: state.uiState.sideNavOpen
    }
}

function matchDispatchToProps(dispatch){
    return bindActionCreators({toggleSideNav: toggleSideNav}, dispatch);
}

export default connect(mapStateToProps, matchDispatchToProps)(SideNav);