import React, { Component } from 'react';
import { BrowserRouter, Route } from 'react-router-dom';
import './App.css';

import TopNav from './components/top_nav/TopNav';
import SideNav from './components/side_nav/SideNav';
import BackDrop from './components/side_nav/back-drop/BackDrop';
import Home from './components/home_page/HomePage';
import ShopRedux from './components/shop_page/ShopPageRedux';
import Login from './components/login_page/LoginPage';
import User from './components/user_page/UserPage';
import New from './components/create_account_page/CreateAccountPage';
import Sock from './components/sock_page/SockPage';
import Cart from './components/shopping_cart_page/ShoppingCartPage';
import OrderComplete from './components/order_complete_page/OrderCompletePage';
import Contact from './components/contact_page/ContactPage';
import DefaultFooter from './components/default_footer/DefaultFooter';

class App extends Component {

  render() {
    return (
      <BrowserRouter>
        <div className="App">
          <SideNav />
          <BackDrop />
          <TopNav />
          <Route exact path='/' component={Home} />
          <Route path='/all' component={ShopRedux} />
          <Route path='/women' component={ShopRedux} />
          <Route path='/men' component={ShopRedux} />
          <Route path='/kids' component={ShopRedux} />
          <Route path='/user' component={User} />
          <Route path='/login' component={Login} />
          <Route path='/new' component={New} />
          <Route path='/sock/:code' component={Sock} />
          <Route path='/cart' component={Cart} />
          <Route path='/complete' component={OrderComplete} />
          <Route path='/contact' component={Contact} />
          <DefaultFooter />
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
