import React from 'react';
import { Link } from 'react-router-dom';
import './SockTile.css';

import sock from './../../../images/WhiteSockSmall.png';


//src={props.image} in img

const SockTile = props => {
    return (
        <Link to={{ pathname:`/sock/${props.id}`}}>
            <div className="sock-tile-container">
                <div className="image-container">
                    <img className="image" src={sock} alt=""></img>
                </div>
                <p className="sock-name">{props.name}</p>
                <p>€ {props.price}</p>
            </div>
        </Link>
    )
}

export default SockTile;