import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import './PageCartRecord.css';

import { removeFromCart } from '../../../actions/actions';

class CartRecord extends Component {

    componentDidMount() {
        window.scrollTo(0, 0);
    }

    render() {
        return (
            <div className="page-cart-record-container">
                <div className="page-cart-record-image">
                </div>
                <div className="page-cart-record-information">
                    <div className="page-cart-record-title">{this.props.name}</div>
                    size: XS
                    <div className="page-cart-record-quantity">amount: {this.props.quantity}</div>
                </div>
                <div className="page-cart-record-delete-button">
                    <button onClick={() => this.props.removeFromCart(this.props.id)}>X</button>
                </div>
            </div>
        )
    }
}

function matchDispatchToProps(dispatch) {
    return bindActionCreators({ removeFromCart: removeFromCart }, dispatch);
}

export default connect(null, matchDispatchToProps)(CartRecord);