import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import './ShoppingCart.css';


import CartRecord from './cart_record/CartRecord'

class ShoppingCart extends Component {

    render() {
        if (this.props.socks.length < 1) {
            return (
                <div className="shopping-cart-container" style={{ visibility: this.props.visibility }}>
                    <p>Shopping cart empty :(</p>
                </div>
            )

        } else {
            return (
                <div className="shopping-cart-container" style={{ visibility: this.props.visibility }}>
                    <p className="shopping-cart-title">Shopping cart</p>
                    <div className="shopping-cart-items">
                        {this.props.socks.map(sock =>
                            <CartRecord key={sock.id}{...sock} />
                        )}
                    </div>
                    <Link to={{ pathname: '/cart' }}>
                        <button className="shopping-cart-button">Go to cart</button>
                    </Link>
                </div>
            )
        }
    }
}

function mapStateToProps(state) {
    return {
        visibility: state.uiState.cart,
        socks: state.cartState
    }
}

export default connect(mapStateToProps)(ShoppingCart);