import React, { Component } from 'react';
import { Redirect, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import localforage from 'localforage';
import LoadingAnimation from './../loading_animation/LoadingAnimation';


import './UserPage.css'

class UserPage extends Component {

    constructor(props){
        super(props);
        this.state = {
            user: {},
            isLoaded: false
        }
    }

    componentDidMount() {
        if (localforage.getItem('user') !== null) {
            localforage.getItem('user')
            .then(value => {
                this.setState({
                    user: value,
                    isLoaded: true
                })
            });
        } else {
            console.log("Geen data beschikbaar");
            this.setState({
                isLoaded: true
            })
        }
    }
    
    render() {
        var { user, isLoaded } = this.state;

        if (!isLoaded) {
            return (
                <div className="user-page-container">
                    <LoadingAnimation />
                </div>);

        } else {
            // if (JSON.stringify(this.props.user) === "{}") {
            //     return (
            //         <Redirect to='/login' />
            //     )
            if (user === null) {
                return (
                    <Redirect to='/login' />
                )
                // } else {
                //     return (
                //         <div className="user-page-container">
                //             <h1>Welcome, {this.props.user.name}!</h1>
                //         </div>
                //     );
                // }
            } else {
                return (
                    <div className="user-page-container">
                        <h1>Welcome, {user.name}!</h1>
                        <Link to="/all" onClick={()=>localforage.removeItem('user')}>Logout</Link>
                    </div>
                );
            }
        }
    }
}

function mapStateToProps(state) {
    return {
        user: state.userState
    }
}

export default connect(mapStateToProps)(UserPage);