import React, { Component } from 'react';
import './DefaultBanner.css';

class DefaultBanner extends Component {

    constructor(props) {
        super(props);
        this.state = {
            category: ""
        }
    }

    componentDidMount() {
        switch (window.location.pathname) {
            case "/men":
                this.setState({ category: "men" });
                break;
            case "/women":
                this.setState({ category: "women" });
                break;
            case "/kids":
                this.setState({ category: "kids" });
                break;
            case "/all":
                this.setState({ category: "all socks" });
                break;
            default:
                this.setState({ category: "socks" });
                break;
        }
    }

    render() {
        return (
            <div className="default-banner-container">
                {this.state.category}
            </div>
        )
    }
}

export default DefaultBanner;