export const RESET_FILTER = 'RESET_FILTER';
export const ADD_TO_CART = 'ADD_ITEM_TO_CART';
export const REMOVE_FROM_CART = 'REMOVE_FROM_CART';
export const OPEN_MINI_CART = 'OPEN_MINI_CART';
export const CLOSE_MINI_CART = 'CLOSE_MINI_CART';
export const TOGGLE_SIDE_NAV = 'TOGGLE_SIDE_NAV';
export const LOGIN_USER = 'LOGIN_USER';
export const LOGOUT_USER = 'LOGOUT_USER';
export const CREATE_ACCOUNT = 'CREATE_ACCOUNT';
export const EMPTY_CART = 'EMPTY_CART';


/* Filter */
export const resetFilter = () => {
    return {
        type: RESET_FILTER
    }
}


/* Cart */
export const addToCart = (sock) => {
    return {
        type: ADD_TO_CART,
        payload: sock
    }
}

export const removeFromCart = (sock) => {
    return {
        type: REMOVE_FROM_CART,
        payload: sock
    }
}

export const emptyCart = () => {
    return {
        type: EMPTY_CART
    }
}


/* UI */
export const openMiniCart = () => {
    return {
        type: OPEN_MINI_CART
    }
}

export const closeMiniCart = () => {
    return {
        type: CLOSE_MINI_CART
    }

}

export const toggleSideNav = () => {
    return {
        type: TOGGLE_SIDE_NAV
    }
}

/* USER */ // niet meer nodig
export const loginUser = (user) => {
    return {
        type: LOGIN_USER,
        payload: user
    }
}

// niet meer nodig
export const logoutUser = () => {
    return {
        type: LOGOUT_USER
    }
}

// niet meer nodig
export const createAccount = (newUser) => {
    return {
        type: CREATE_ACCOUNT,
        payload: newUser
    }
}