import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { emptyCart } from './../../actions/actions';
import './OrderCompletePage.css';

class OrderCompletePage extends Component {

    componentDidMount() {
        this.props.emptyCart();
    }

    render() {
        if (!navigator.onLine) {
            return (
                <div className="order-complete-page-container">
                    <div className="order-complete-page-message">
                        <p className="complete-header oranje">Bestelling nog niet verzonden!</p>
                        <p>Verbind met internet om je bestelling te verzenden, dan laten wij je weten wanneer je bestelling geplaatst is</p>
                    </div>
                </div>
            )
        } else {
            return (
                <div className="order-complete-page-container">
                    <div className="order-complete-page-message">
                        <p className="complete-header groen">Bestelling succesvol geplaatst!</p>
                        <p>We proberen je bestelling zo snel mogelijk naar je toe te sturen</p>
                    </div>
                </div>
            )
        }
    }
}

function matchDispatchToProps(dispatch) {
    return bindActionCreators({ emptyCart: emptyCart }, dispatch);
}

export default connect(null, matchDispatchToProps)(OrderCompletePage);