import React from 'react';
import {Link} from 'react-router-dom';
import './HomePageBanner.css'

const HomePageBanner = () => {
    return (
        <div className="home-page-banner-container">
            <span>
                <span className="home-page-banner-title">
                    We will sock you
                </span>
                <span className="home-page-banner-subtitle">
                    check out the&nbsp;<Link to='/all'>new</Link>&nbsp;collection
                </span>
            </span>
        </div>
    )
}

export default HomePageBanner;