import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { createAccount } from '../../actions/actions';

import './CreateAccountPage.css'

class CreateAccountPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name:'',
            username: '',
            password: '',
            createAccountFailedAlert:false
        }
    }

    render() {
        return (
            <div className="create-account-page-container">
                <div className="create-account-form-container">
                    <div className="create-account-form">
                        <p>Create Account</p>
                        <input value={this.state.name} onChange={input => this.updateName(input)} placeholder="Name" />
                        <br />
                        <input value={this.state.username} onChange={input => this.updateUsername(input)} placeholder="Username" />
                        <br />
                        <input value={this.state.password} onChange={input => this.updatePassword(input)} type="password" placeholder="Password" />
                        <br />
                        <button onClick={() => { this.createAccount() }}>Create</button>
                        {!window.navigator.onLine ? <p className="create-account-failed-alert">Internet is required to create an account</p> : null}
                        {this.state.createAccountFailedAlert ? <p className="create-account-failed-alert">Failed to create account</p> : null}
                    </div>
                </div>
            </div>
        );
    }
    updateName(input) {
        this.setState({
            name: input.target.value
        });
    }

    updateUsername(input) {
        this.setState({
            username: input.target.value
        });
    }

    updatePassword(input) {
        this.setState({
            password: input.target.value
        });
    }

    createAccount() {
        fetch('https://webshop-api.herokuapp.com/user/new', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({name:this.state.name, username:this.state.username, password: this.state.password})
        })
        .then(res => {
            if (res.ok) {
                return res.json()
            }
            throw new Error("Account aanmaken is mislukt")
        })
        .then(data => {
            this.props.createAccount(data)
            this.props.history.push("/all")
        })
        .catch(err => {
            console.log(err)
            this.setState({createAccountFailedAlert:true})
        });
    }
}

function matchDispatchToProps(dispatch) {
    return bindActionCreators({createAccount: createAccount}, dispatch)
}

export default connect(null, matchDispatchToProps)(CreateAccountPage);