import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { openMiniCart, closeMiniCart, toggleSideNav } from '../../actions/actions';

import './TopNav.css';
import hamburger from './../../images/hamburger.png';
import cart from './../../images/cart.png';
import user from './../../images/user.png';

import ShoppingCart from './../shopping_cart/ShoppingCart';

class TopNav extends Component {
    render() {
        return (
            <div className="top-nav-container">
                <div className="top-nav-inner-container">
                    <div className="hamburger-container" onClick={() => this.props.toggleSideNav()}>
                        <img className="icon" alt="icon" src={hamburger}></img>
                    </div>
                    <Link to='/'>
                        <div className="logo-container">
                            Sock Market <span role="img" aria-label="logo">📈</span>
                        </div>
                    </Link>
                    <div className="top-nav-container-right">
                        <nav className="nav-container">
                            <ul>
                                <li><Link to='/all'>All socks</Link></li>
                                <li><Link to='/women'>Women</Link></li>
                                <li><Link to='/men'>Men</Link></li>
                                <li><Link to='/kids'>Kids</Link></li>
                            </ul>
                        </nav>

                        <div className="profile-picture-container">
                            <Link to='/user'>
                                <img className="icon" alt='user' src={user}></img>
                            </Link>
                        </div>

                        <div className="cart-container" onMouseEnter={() => this.props.openMiniCart()} onMouseLeave={() => { this.props.closeMiniCart() }}>
                            { this.props.count > 0 ? <div className="cart-counter">{this.props.count}</div> : null }
                            <Link to='/cart'>
                                <img className="icon" alt='cart' src={cart}></img>
                            </Link>
                            <ShoppingCart />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

function matchDispatchToProps(dispatch) {
    return bindActionCreators({ openMiniCart: openMiniCart, closeMiniCart: closeMiniCart, toggleSideNav: toggleSideNav }, dispatch);
}

function mapStateToProps(state) {
    return {
        count: state.cartState.length        
    }
}

export default connect(mapStateToProps, matchDispatchToProps)(TopNav);