import React, { Component } from 'react';
import localforage from 'localforage';
import './ShopPage.css';

import DefaultBanner from '../default_banner/DefaultBanner';
import SockTile from './sock_tile/SockTile';
import LoadingAnimation from './../loading_animation/LoadingAnimation';

class ShopPageRedux extends Component {

    constructor(props) {
        super(props);
        this.state = {
            items: [],
            isLoaded: false,
        }
    }

    componentDidMount() {
        window.scrollTo(0, 0);

        if (window.navigator.onLine) {
            fetch('https://webshop-api.herokuapp.com/sock')
                .then(res => res.json())
                .then(json => {
                    this.setState({
                        isLoaded: true,
                        items: json,
                    })
                    localforage.setItem('allSocks', json)
                })
        } else {

            localforage.getItem('allSocks').then(value => {
                if (value !== null){
                    this.setState({
                        isLoaded: true,
                        items: value,
                    })
                } else {
                    console.log("Geen data beschikbaar");
                }
            })
        }
    }

    render() {

        var { isLoaded, items } = this.state;
        var forbiddenCodes = [1];

        if (!isLoaded) {
            return (
                <div className="shop-page-container">
                    <DefaultBanner />
                    { navigator.onLine ? <LoadingAnimation /> : <div>Connect to the internet to load Socks</div>}
                </div>);
        } else {
            return (
                <main className="shop-page-container">
                    <DefaultBanner />
                    <div className="stock-container">
                        {items
                            .filter(sock => {
                                return !forbiddenCodes.some((forbiddenCode) => { //remove "!" to change forbidden codes to accepted codes
                                    return sock.name === forbiddenCode;
                                })
                            })
                            .map(item =>
                                <SockTile key={item.id} {...item} />
                            )}
                    </div>
                </main>
            )
        }
    }
}

export default ShopPageRedux;