import { ADD_TO_CART, REMOVE_FROM_CART, EMPTY_CART } from '../actions/actions';

export default (state = [], action) => {

    switch (action.type) {
        case ADD_TO_CART:

            let cartSock = action.payload;

            if (state.some((sock) => { return cartSock.id === sock.id })) { // && Size uniek

                state.find(sock => cartSock.name === sock.name).quantity += 1;

                return state.map((sock, id) => {
                    if (id !== cartSock.id) {
                        return sock
                    }

                    return {
                        cartSock
                    }
                })

            } else {
                cartSock.quantity = 1;
                return [
                    ...state, cartSock
                ]
            }


        case REMOVE_FROM_CART:
            return state.filter((sock) => sock.id !== action.payload);

        case EMPTY_CART:
            return [];

        default:
            return state;
    }
}