import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { addToCart } from '../../actions/actions'
import './SockPage.css';
import LoadingAnimation from './../loading_animation/LoadingAnimation';
import localforage from 'localforage';

import sockImage from './../../images/WhiteSockMedium.png';

//src={this.state.sock.image}


class SockPage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            param: props.match.params.code,
            isLoaded: false,
            sock: {},
            sizes: {},
            selectedSize: "Size",
            sizeAlert: false
        }
    }

    componentDidMount() {
        window.scrollTo(0, 0);

        if (window.navigator.onLine) {
            fetch(`https://webshop-api.herokuapp.com/sock/${this.state.param}`)
                .then(res => res.json())
                .then(json => {
                    this.setState({ sock: json })
                });
            fetch(`https://webshop-api.herokuapp.com/sock/stock/${this.state.param}`) // 2e keer zit stock al in de sock van de fetch hierboven
                .then(res => res.json())
                // .then(json => { Object.keys(json).map(function(key){
                //     return [Number(key), json[key]];
                //     })    
                // })
                .then(json => {
                    this.setState({ sizes: json, isLoaded: true })
                });


        } else {
            if (localforage.getItem('allSocks') !== null) {
                localforage.getItem('allSocks').then(allSocks => {
                    allSocks.forEach(sock => {
                        if(String(sock.id) === String(this.state.param)){
                            this.setState({sock:sock});
                            console.log("Nieuwe sock is in de state geplaatst")
                        }
                    });

                    this.setState({
                        isLoaded:true,
                        sizes: {'xs': 0, 's': 0, 'm': 0, 'l': 0, 'xl': 0}
                    })
                });
            } else {
                console.log("Geen data beschikbaar");
            }
        }


    }

    render() {

        var { isLoaded, sock, sizes } = this.state;
        var sizeArray = Object.keys(sizes).map(key => {
            return [key, sizes[key]]
        })

        if (isLoaded) {
            return (
                <div className="sock-page-container">
                    <div className="sock-display">
                        <img alt='sock' src={sockImage}></img>
                    </div>
                    <div className="sock-order-section-container">
                        <div className="sock-order-section">
                            <div className="sock-page-name">{sock.name}</div>
                            <div className="sock-page-description">{sock.description}</div>
                            <div className="sock-page-price">€ {sock.price}</div>
                            {!navigator.onLine ? <div className="wrong-size-alert">Connect to the internet to get available sizes</div> : null}
                            <select onChange={this.updateSelectedSize} value={this.state.selectedSize}>
                                <option value="Size">Size</option>

                                {sizeArray.map((size, index) => {

                                    if (size[0] !== "id") {

                                        if (size[1] > 0) {

                                            return <option key={index} value={size[0]}>{size[0].toUpperCase()}</option>

                                        } else {

                                            return <option key={index} value={size[0]} disabled>{size[0].toUpperCase()}</option>

                                        }
                                    } else {
                                        return null;
                                    }
                                })}

                            </select>
                            <button onClick={() => {
                                if (this.state.selectedSize === "Size") {
                                    this.setState({ sizeAlert: true })
                                } else {
                                    this.setState({ sizeAlert: false })
                                    this.props.addToCart(this.state.sock)
                                }
                            }}>
                                Kopen
                            </button>
                            {this.state.sizeAlert ? <div className="wrong-size-alert">Choose a size!</div> : null}
                        </div>
                    </div>
                    <div className="sock-details-title">
                        <p>Materials</p>
                    </div>
                    <div className="sock-details">
                        <p>60% cotton</p>
                        <p>39% polyester</p>
                        <p>1% spandex</p>
                    </div>
                    <div className="sock-details-title">
                        <p>Color</p>
                    </div>
                    <div className="sock-details">
                        <p>white</p>
                    </div>

                    <div className="sock-details-title">
                        <p>Product code</p>
                    </div>
                    <div className="sock-details">
                        <p>47FHS93F8309</p>
                    </div>

                    <div className="sock-details-title">
                        <p>Free delivery</p>
                    </div>
                    <div className="sock-details">
                        <p>for orders over 20,-</p>
                    </div>
                </div>
            )
        } else {
            return <LoadingAnimation />
        }
    }

    updateSelectedSize = (event) => {
        this.setState({ selectedSize: event.target.value });
    }
}

function matchDispatchToProps(dispatch) {
    return bindActionCreators({ addToCart: addToCart }, dispatch);
}

export default connect(null, matchDispatchToProps)(SockPage); // ipv null map state to props