import { OPEN_MINI_CART, CLOSE_MINI_CART, TOGGLE_SIDE_NAV } from '../actions/actions';

export default (state = { cart: 'hidden', sideNavOpen: false }, action) => {
    switch (action.type) {
        case OPEN_MINI_CART:
            return {
                ...state,
                cart: 'visible'
            }

        case CLOSE_MINI_CART:
            return {
                ...state,
                cart: 'hidden'
            }

        case TOGGLE_SIDE_NAV:
            let newSideNavOpen = !state.sideNavOpen

            return {
                ...state,
                sideNavOpen: newSideNavOpen,
            }

        default:
            return state;
    }
}